import * as React from "react";
import moment from 'moment'

import effect from '../../assets/img/card/effect.png'
import noEffect from '../../assets/img/card/noeffect.png'
import outTime from '../../assets/img/card/outTime.png'

import './card.less';
interface ISprops {
    [propName: string]: any,

}
interface IState {
    time:string
}

export default class ChildCard extends React.Component<ISprops, IState>{
    constructor(props: any) {
        super(props);
        this.state = {
            time:''
        }
    }
    getCourseTime = (data) => {
        let startDate = moment(data.start_time).format('YYYY/MM/DD');
        let endDate = moment(data.end_time).format('YYYY/MM/DD');
        return `${startDate} -${endDate}`;
    }
    checkoutCardType = (data) => {
        switch (data.time_status) {
            case 0:
            return 'child-card';

            case 1:
            return 'child-card no-effect'

            default:
            return 'child-card out-time'
        }
    }
    checkImgType = (data) => {
        switch (data.time_status) {
            case 0:
            return effect

            case 1:
            return noEffect

            default:
            return outTime
        }
    }
    componentDidMount() {
        
    }
    render() {
        const { data } = this.props;
        const courseTime = this.getCourseTime(data);
        return (
            <div className={this.checkoutCardType(data)}>
                <img src={this.checkImgType(data)} alt=""/>
                <p className="timer"><span>{data.surplus_number}</span>次</p>
                <p>可用次数</p>
                <p className="time">{courseTime}</p>
            </div>

        )
    }

}
