import * as React from "react";
import { getCardDetail } from '../../api/card'
import CardTime from '../../components/card/cardTime'
import ChildCard from '../../components/card/childCard'

import './index.less'

interface ISprops {
    [propName: string]: any;
}
interface IState {
    detailData: any
}

export default class CardDetail extends React.Component<ISprops, IState>{
    constructor(props: ISprops) {
        super(props);
        this.state = {
            detailData: {}
        }
    }
    componentDidMount() {
        document.title = '会员卡详情';
        let params = {
            card_id: this.props.match.params.id
        }
        getCardDetail(params).then((data) => {
            if (data.status === 0) {
                this.setState({
                    detailData: data.data
                })
            }
        })
    }
    render() {
        const { detailData } = this.state;
        return (
            <div className="card-info">
                <CardTime data={detailData} />

                <div className="card-child">
                    {
                        detailData.child_cards ?
                            <div className="card-child-i clearfix">
                                {
                                    detailData.child_cards.map( (el,index) => {
                                        return (
                                            <ChildCard data={el} key={index}/>
                                        )
                                    })
                                }
                                
                            </div>
                            :
                            ''
                            
                            
                    }

                </div>

            </div>
        )
    }

}
